let wv;
// import getpixel from '../../range/getpixel.js'
import rating from '../../components/rating.vue'
export default {
	data() {
		return {
			detail: {},
			summary: '',
			moreDetail: {},
			popular_reviews: [],
			moreInfo: {}
		}
	},
	computed: {

	},
	beforeCreate: function() {
		uni.showLoading({
			title: 'loading...'
		})
	},
	onReady: function() {

	},
	onLoad(data) {

		// ...........?
		let info = JSON.parse(data.info);
		this.detail = info;
		uni.request({
			url: `https://douban.uieee.com/v2/movie/subject/${info.id}?apikey=0df993c66c0c636e29ecbb5344252a4a`,
			success: (res) => {
				// console.log(res.data);
				uni.setNavigationBarTitle({
					title: res.data.title
				});
				// debugger;
				for (let i = 0; i < res.data.casts.length; i++) {
					let pictureResult = res.data.casts[i].avatars.small.replace('https://', 'https://images.weserv.nl/?url=');
					res.data.casts[i].avatars.small = pictureResult;
				}
				this.moreInfo = res;
				this.moreDetail = res;
				this.summary = res.data.summary;
				this.popular_reviews = res.data.popular_reviews;
				console.log(res.data.images.small)
				uni.downloadFile({
					url: res.data.images.small,
					success: (res) => {
						if (res.statusCode === 200) {
							console.log('下载成功');
							
						}
					},
				});

			}
		})


	},
	mounted: function() {
		setTimeout(() => {
			uni.hideLoading()
		}, 300);


	},
	methods: {

	},
	components: {
		rating
	}
}
